.trips{
    margin: 80px auto;
    margin-top: 2%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.trip img{
    width: 100%;
    border-radius: 10px;
    display: block;
}

.trip{
    flex-basis: 31%;
    position: relative;
}

.caption{
    border-radius: 10px;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background: rgba(0, 15, 152, 0.3);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: #fff;
    cursor: pointer;
    opacity: 0;
    padding-top: 70%;
    transition: 0.4s;
}

.caption p{
    width: 60px;
    margin-bottom: 10px;
}

.trip:hover .caption{
    opacity: 1;
    padding-top: 0;
}

@media (max-width: 650px){
    .trips{
        flex-direction: column;
        margin-bottom: 0;
    }

    .trip{
        flex-basis: 100%;
        margin: 20px;
    }
}