.about{
    margin: 100px auto;
    margin-top: 10%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 90%;
}

.about-left{
    flex-basis: 50%;
}

.about-left h3{
    margin-bottom: 5px;
    font-size: 25px;
    color: #212EA0;
}

.about-left h2{
    margin-bottom: 7px;
}

.about-right{
    flex-basis: 60%;
}

.about_img{
    width: 90%;
    border-radius: 10px;
}

/*Media Query*/

@media (max-width: 800px){
    .about{
        flex-direction: column;
        margin-bottom: 0;
    }

    .about-left{
        flex-basis: 100%;
        margin: 20px;
        order: 2;
    }

    .about-right{
        flex-basis: 100%;
    }

    .about_img{
        width: 400px;
        /* margin-right: 60px; */
        margin-left: 10px;
        border-radius: 10px;
    }
}

@media (max-width: 400px){
    .about_img{
        width: 300px;
        /* margin-right: 60px; */
        margin-left: 1px;
        border-radius: 10px;
    }
}