.hero{
    width: 100%;
    min-height: 100vh;
    background: linear-gradient(rgba(0, 0, 255, 0.2),rgba(0, 0, 255, 0.2)),url('../../assets/header_img.jpg');
    background-size: cover;
    background-position: center;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
}

.hero-text{
    text-align: center;
    max-width: 800px;
}

.hero-text h1{
    font-size: 35px;
    font-weight: 600;
}

.hero-text p{
    max-width: 700px;
    margin: 10px auto 20px;
}

/* Media Query*/

@media (max-width: 850px){
    .hero-text h1{
        font-size: 30px;
    }
}

@media (max-width: 650px){
    .hero-text h1{
        font-size: 25px;
        max-width: 400px;
        margin: auto;
    }

    .hero-text p{
        font-size: 10px;
        margin: 10px auto;
    }
}