.contact{
justify-content: space-between;
width: 80%;
margin: 0 150px;
}

.contact-col{
    margin: 80px auto;
    margin-top: 2%;
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}
.contact-col p{
    margin-top: 20px;
    font-size: 20px;
    text-align: center;
}
.contact-col ul{
    display: flex;
    margin: 40px;
    margin-left: 2%;
    width: 90%;
    list-style: none;
    font-size: 18px;
    justify-content: space-between;
}






.icon{
    color: #212EA0;
    width: 15px;
    flex-basis: 31%;
    /* position: relative; */
    
}

