@media (max-width: 400px){
    .contact{
        
        justify-content: center;
        margin-left: 30px;
    }

    .contact-col ul{
        display: flex;
        flex-direction: column;
        margin-bottom: 0;
    }

    .icon{
        width: 20px;
    }

    .contact-col ul li{
        width: 120%;
        margin-right: 10%;
        margin-bottom: 25px;
